<template>
    <div>
        <navbar title="公告" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <ul class="myList">
                <li v-for="item in noticeList"><router-link :to="{path:'/noticedetail', query:{noticeId:item.id}}" ><i class="zijin"></i><div class="text"><p>{{item.title}}</p><p><img  src="@/static/images/my/time_icon@2x.png" alt=""><i>{{item.create_time}}</i></p></div><span></span></router-link><img v-if="item.new" class="new" src="@/static/images/my/new_icon@2x.png" alt=""></li>
            </ul>
        </div>
    </div>
</template>

<script>
    import {notice} from "../../api/center";
    import { toast, Dialog } from 'vant';
    import navbar from "@/components/navbar";
    export default {
        name: "Notice",
        components: {
            navbar
        },
        data()
        {
            return {
                noticeList:[]
            }
        },
        mounted()
        {
            this.getNotice()
        },
        methods:{

            getNotice()
            {
                notice().then(res => {
                    this.noticeList = res
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/notice.css";
</style>



